"use client";

import { ReactNode } from "react";
import clsx from "clsx";

export function Section({
  id,
  title,
  eyebrow,
  children,
  className
}: {
  id?: string;
  title: string;
  eyebrow?: string;
  children: ReactNode;
  className?: string;
}) {
  return (
    <section
      id={id}
      className={clsx("scroll-mt-24 py-14 sm:py-20", className)}
    >
      <div className="mb-8">
        {eyebrow && (
          <p className="text-xs font-semibold uppercase tracking-[0.18em] text-text-mutedLight dark:text-text-mutedDark">
            {eyebrow}
          </p>
        )}
        <h2 className="mt-2 text-2xl sm:text-3xl font-semibold text-text-light dark:text-text-dark">
          {title}
        </h2>
      </div>
      {children}
    </section>
  );
}


